import { CardTypeCategory } from "../../../../core/src/main";
import { computed, defineComponent } from "@vue/composition-api";
import YgoCard from "../YgoCard.vue";
import Draggable from "vuedraggable";
import { createMoveFromBuilderValidator } from "../../composition/controller/dragging";
export default defineComponent({
    props: {
        matches: {
            required: true,
            type: Array,
        },
        dragGroup: {
            required: true,
            type: String,
        },
    },
    components: {
        YgoCard,
        Draggable,
    },
    setup(props, context) {
        const CARD_DISPLAY_LIMIT = 50;
        const limitedMatches = computed(() => props.matches.slice(0, CARD_DISPLAY_LIMIT));
        const typeText = (card) => card.type.category === CardTypeCategory.MONSTER
            ? card.type.name
            : card.type.category;
        const subTypeText = (card) => card.type.category === CardTypeCategory.MONSTER
            ? `${card.attribute}/${card.subType}`
            : card.subType;
        const canMove = createMoveFromBuilderValidator(context);
        return { limitedMatches, typeText, subTypeText, canMove };
    },
});
