import { CardTypeCategory, DeckPart, DefaultDeckPartConfig, getLogger, } from "../../../../core/src/main";
import { computed, defineComponent } from "@vue/composition-api";
import YgoPrice from "../YgoPrice.vue";
import YgoCard from "../YgoCard.vue";
import { applicationContainer } from "../../inversify.config";
import { APPLICATION_TYPES } from "../../types";
import Draggable from "vuedraggable";
import { DECK_PART_CARDS_ADD, DECK_PART_CARDS_REMOVE, DECK_PART_CARDS_REORDER, } from "../../store/modules/deck";
import { appStore } from "../../composition/state/appStore";
import { removeEnd } from "lightdash";
import { createMoveInDeckPartValidator, DECK_PART_PROP, } from "../../composition/controller/dragging";
const cardService = applicationContainer.get(APPLICATION_TYPES.CardService);
const cardDatabase = applicationContainer.get(APPLICATION_TYPES.CardDatabase);
/**
 * Calculates count of card types.
 * For main and side deck, count will be split by monster, spell, etc.,
 * whereas it will be split by monster subtype for the extra deck.
 *
 * @private
 * @param deckPart Deck-part that is being used.
 * @param cards Cards to analyse.
 * @return Array of type and count pairs.
 */
const calculateDetailedTypeStats = (deckPart, cards) => {
    if (deckPart === DeckPart.EXTRA) {
        const countedByType = cardService.countByType(cards);
        return cardDatabase
            .getTypes(CardTypeCategory.MONSTER)
            .filter((cardType) => countedByType.has(cardType) &&
            countedByType.get(cardType) > 0)
            .map((cardType) => [
            removeEnd(cardType.name, " Monster"),
            countedByType.get(cardType),
        ]);
    }
    const countedByTypeCategory = cardService.countByTypeCategory(cards);
    return Object.values(CardTypeCategory)
        .filter((typeCategory) => countedByTypeCategory.has(typeCategory) &&
        countedByTypeCategory.get(typeCategory) > 0)
        .map((typeCategory) => [
        typeCategory,
        countedByTypeCategory.get(typeCategory),
    ]);
};
const logger = getLogger("YgoDeckPart");
export default defineComponent({
    components: {
        YgoPrice,
        YgoCard,
        Draggable,
    },
    props: {
        [DECK_PART_PROP]: {
            required: true,
            type: String,
        },
        dragGroup: {
            required: true,
            type: String,
        },
    },
    setup: (props, context) => {
        const deckPartConfig = computed(() => DefaultDeckPartConfig[props.deckPart]);
        const cards = computed(() => appStore(context).state.deck.active.parts[props.deckPart]);
        const deckPartEmpty = computed(() => cards.value.length === 0);
        const deckPartStats = computed(() => {
            const currentCards = cards.value;
            const base = `${currentCards.length} Cards`;
            if (currentCards.length === 0) {
                return base;
            }
            const details = calculateDetailedTypeStats(props.deckPart, currentCards).map(([type, count]) => `${count} ${type}`);
            return `${base} (${details.join(" | ")})`;
        });
        const addCard = (card, newIndex) => appStore(context).commit(DECK_PART_CARDS_ADD, {
            deckPart: props.deckPart,
            card,
            newIndex,
        });
        const removeCard = (card, oldIndex) => appStore(context).commit(DECK_PART_CARDS_REMOVE, {
            deckPart: props.deckPart,
            card,
            oldIndex,
        });
        const reorderCard = (card, oldIndex, newIndex) => appStore(context).commit(DECK_PART_CARDS_REORDER, {
            deckPart: props.deckPart,
            card,
            oldIndex,
            newIndex,
        });
        const onChange = (e) => {
            if (e.removed != null) {
                removeCard(e.removed.element, e.removed.oldIndex);
            }
            else if (e.added != null) {
                addCard(e.added.element, e.added.newIndex);
            }
            else if (e.moved != null) {
                reorderCard(e.moved.element, e.moved.oldIndex, e.moved.newIndex);
            }
            else {
                logger.warn("Unexpected drag event type.", e);
            }
        };
        const canMove = createMoveInDeckPartValidator(context, props.deckPart);
        return {
            deckPartConfig,
            cards,
            deckPartStats,
            deckPartEmpty,
            onChange,
            canMove,
        };
    },
});
